import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { ApicallService } from 'src/app/services/CallApi/apicall.service';
import { GlobalService } from 'src/app/services/data/global.service';
import { ModalController } from "@ionic/angular";
import { ModalPage } from './../../pages/modal/modal.page';
import { NgxPrintElementService } from 'ngx-print-element';
import { format, parseISO } from 'date-fns';


@Component({
  selector: 'app-instalment',
  templateUrl: './instalment.page.html',
  styleUrls: ['./instalment.page.scss'],
})
export class InstalmentPage implements OnInit {
  


  public isEnabled:boolean = false
  public link:any=[
    {path:'add-plot'},
    {path:'see-plot'},
    {path:'instalment'},
    ];
    public config = {
      printMode: 'template-popup', // template
      popupProperties: 'toolbar=yes,scrollbars=yes,resizable=yes,top=0,left=0,fullscreen=yes',
      pageTitle: 'Luqman Town',
      templateString: '{{printBody}}<footer>Luqman Town</footer>',
      stylesheets: [{ rel: 'stylesheet', href: 'https://cdn.jsdelivr.net/npm/bootstrap@5.2.2/dist/css/bootstrap.min.css' }],
      styles: ['']
    }

  public Plot_D:any = [ {plot_no:'2' , name:'Sumama' , type:'Commercial' , status:'avaiable' , description:'fjksfalksjhdjgfhdjghdksdjhgkjdshgfhsjahf' ,
                         advance:'30000' , total_payment:'342355' , total_installment:'21' , remaining_payment:'482348' },
                         {plot_no:'2' , name:'Sumama' , type:'Commercial' , status:'avaiable' , description:'fjksfalksjffjdfjkddjkhsjahf' ,
                         advance:'30000' , total_payment:'342355' , total_installment:'21' , remaining_payment:'482348' } ];
  public spec_plot: any;
  user:any;
  public p_no: any ;
  public Selec_Customer:any;
  public Plot_Data:any;
  public allinstallments:any;
  public install_no: any;
  public _7Starinstall_no: any;
  public next_installment:any = {p_id:'' , customer_id:'', customer_name:'' ,installment_no:'', amount:'', remaning_payment:'', upcoming_installment:'',i_month:'', a_id:''} ;
  public next_7starinstallment:any = {p_id:'' , customer_id:'', customer_name:'' ,installment_no:'', amount:'', remaning_payment:'',installment_date:'', upcoming_installment:''} ;
  public installment_by_id:any;
  public Category: any = 'View all installment';
      public selectTabs:any = 'Baba Fareed';
  public all7Starinstallments: any;
 public installmentrdata: any;
  constructor(public apiCall:ApicallService, private route: Router , public modal : ModalController , public global: GlobalService, public print: NgxPrintElementService) { }
 
  ngOnInit() {
    this.get_Booking();
    this.get_Plot();
 
  }


      // Get function of plot_data
      async get_Booking() {
        this.apiCall.api_get_all_boooking();
        await this.global.Allbooking.subscribe(user => {
          console.log(user);
          // this.Plot_D = user;
         });
       }

      // Get function for customer
     async Get_Customer() {
      this.apiCall.api_get_customer();
      await this.global.Customer.subscribe(data => {
        this.Selec_Customer = data;
        console.log(this.Selec_Customer);
       });
     }
  
     async get_Plot() {
      this.apiCall.api_getPlots();
       this.global.Plots.subscribe(user => {
        console.log(user);
        this.Plot_Data = user;        
       });
     }


  Property(){
    this.route.navigate(['property'])
  }
  Plot(){
    this.route.navigate(['plots'])
  }
  Instalment(){
    this.route.navigate(['instalment'])
  }

  Refresh(){
    this.apiCall.api_get_all_boooking();
  }
  Home(){
    this.route.navigate(['home'])
  }

  async get_bookedplots($event){
    console.log($event.target.value)
    await this.apiCall.api_getbooked_plots($event.target.value);
    await this.global.BookedProperties.subscribe( res => {
      this.p_no = res
    })
    
    console.log(this.p_no[0].p_id)
    await this.apiCall.api_getlast_InstallmentByp_id(this.p_no[0].p_id);
    this.get_last_install();
  }

  async get_last_install(){
    if(this.selectTabs == 'Baba Fareed'){
      this.global.LumanTownInstallments.subscribe(res => {
        console.log(res);
        this.allinstallments = res;  
        console.log('Baba Fareed');
       });
    }
    if(this.selectTabs == '7 Star'){
      this.global._7Starinstallments.subscribe(res => {
        console.log(res);
        this.all7Starinstallments = res;  
        console.log('7 Star');
       });
    }
    else{
      console.log('no plots');
    }

    // this.global.Getlast_installmentbyid.subscribe(res => {
    //   console.log(res);
    //   this.allinstallments = res;  
    //  });
       if(this.selectTabs == 'Baba Fareed'){
        console.log(this.allinstallments[0].installment_no +'incoming install')
        this.install_no = this.allinstallments[0].installment_no;
        this.install_no++;    
        console.log(this.install_no + 'current isntall') 
       }
       if(this.selectTabs == '7 Star'){
        console.log(this.all7Starinstallments[0].installment_no +'incoming install')
        this._7Starinstall_no = this.all7Starinstallments[0].installment_no;
        this._7Starinstall_no++;    
        console.log(this._7Starinstall_no + 'current isntall') 
       }
       else{
        console.log('no plots');
       }
  }


  installment_amount($event){
    if(this.selectTabs == 'Baba Fareed'){
      console.log($event.target.value);
      const x = $event.target.value;
      this.next_installment.remaning_payment =  this.allinstallments[0].remaning_payment - x;
    }
    if(this.selectTabs == '7 Star'){
      console.log($event.target.value);
      const x = $event.target.value;
      this.next_installment.remaning_payment =  this.all7Starinstallments[0].remaning_payment - x;
    }
    else{
      console.log('no amount');
    }

  }


  month($event){
    console.log($event.target.value);
    this.next_installment.i_month = format(parseISO($event.target.value), 'MMMM');
    console.log(this.next_installment.i_month);
    
  }


  Next_installment(){
    if(this.selectTabs == 'Baba Fareed'){
      console.log(this.allinstallments)
      this.next_installment.p_id = this.allinstallments[0].p_id;
      this.next_installment.customer_id = this.allinstallments[0].customer_id;
      this.next_installment.installment_no =  this.allinstallments[0].installment_no;
      this.next_installment.installment_no++;  
      // this.next_installment.remaning_payment =  this.allinstallments[0].remaning_payment - this.next_installment.amount;
      console.log(this.next_installment.remaning_payment);
      this.next_installment.customer_name =  this.allinstallments[0].name;
      this.next_installment.a_id = 1;
      console.log(this.next_installment.installment_no + 'next install')
      
      console.log(this.next_installment)
    }
    if(this.selectTabs == '7 Star'){
      console.log(this.all7Starinstallments)
      this.next_installment.p_id = this.all7Starinstallments[0].p_id;
      this.next_installment.customer_id = this.all7Starinstallments[0].customer_id;
      this.next_installment.installment_no =  this.all7Starinstallments[0].installment_no;
      this.next_installment.installment_no++;  
      // this.next_installment.remaning_payment =  this.all7Starinstallments[0].remaning_payment - this.next_installment.amount;
      this.next_installment.customer_name =  this.all7Starinstallments[0].name;
      this.next_installment.a_id = 2;
      console.log(this.next_installment.installment_no + 'next install')
      
      console.log(this.next_installment);


    }
    else{
      console.log('no plots');
    }
    // this.route.navigate(['print-installment'],{state:{data:this.next_installment, plot:this.allinstallments}});
    console.log(this.next_installment);
    this.apiCall.api_insert_installment(this.next_installment);
    this.next_installment = {p_id:'' , customer_id:'', customer_name:'' ,installment_no:'', amount:'', remaning_payment:'',installment_date:'', upcoming_installment:'',i_month:'', a_id:''};
    this.route.navigate(['property'])
  }

  print_installment(){
    this.route.navigate(['print-installment'],{state:{data:this.next_installment}});
    console.log(this.next_installment);
  }

  async all_installment(id){

   await this.apiCall.api_getInstallmentByp_id(id)
    this.global.Getinstallmentbyid.subscribe(res => {
      console.log(res);
      this.installment_by_id = res; 
     });
    // this.isEnabled = true;
  }


  async view_all_installment(id) {
    await this.apiCall.api_getInstallmentByp_id(id)
    this.global.Getinstallmentbyid.subscribe(res => {
      console.log(res);
      this.installment_by_id = res; 
     });
this.installmentrdata = this.installment_by_id.filter(x => (x.installment_no > 0))
 console.log(this.installmentrdata)
   const modal = await this.modal.create({
      component: ModalPage,
      cssClass: 'my-modal-class',
      initialBreakpoint: 0.93 ,
      breakpoints: [0, 0.435, 0.93],
      componentProps: {
        'Category' : this.Category,
        'Installment_details' : this.installmentrdata,
      }
    });
    console.log('modal')
    return await modal.present();
  }




}
